<template>
    <div>
        <div class="picture">
            <img v-bind:src="message_file_img" style="height: 472px;width: 396px;"/>
        </div>
        <div class="message">
            <div style="height: 70px; width: 100%;font-size: 25px;font-weight: bold;background: none;word-break: break-all;word-wrap: break-word;">
                &nbsp {{bookname}}
            </div>
            <div style="height: 30px;width: 100%;font-size: 20px;font-family: 'Adobe 黑体 Std R';">
                &nbsp 作者：{{writer}}
            </div>
            <div style="height: 160px; width: 100%;background: none;font-size: 15px;word-break: break-all;word-wrap: break-word;font-family: 黑体;">
                书评：<br>
                {{message_file_evaluation}}
            </div>
            <div style="height: 50px; width: 100%;background: #F0F0F0">
                <span style="color: #888888;font-size: 15px">&nbsp &nbsp ebook售价</span><br>
                <span style="color: crimson; font-size: 15px;">&nbsp &nbsp ¥</span>
                <span style="color: crimson; font-size: 25px;">&nbsp {{price}}</span>
            </div>
            <div style="font-size: 20px; height: 180px;width: 100%;background: none;" class="input-group">
                <br>
                <div class="input-group" style="width: 100px;">
                    <span class="input-group-addon minus" style="cursor: pointer" @click="handleReduce">-</span>
                    <input type="text" class="number form-control input-sm" v-model="amount" />
                    <span class="input-group-addon plus" style="cursor: pointer" @click="handleAdd">+</span>
                </div>
                <span style="font-family: 微软雅黑;color: #888888;font-size: 17px">库存：剩余 &nbsp {{repertory}} &nbsp 件</span>
                <br>
                <br>
                <button class="sbmt_cart" @click="Carting">加入购物车</button>
                <button class="sbmt_buy" @click="Ordering">立即购买</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Detail",
        data() {
            return{
                book_id: this.$route.params.book_id,
                bookname: this.$route.params.bookname,
                writer: this.$route.params.writer,
                message_file_img: "",
                price: this.$route.params.price,
                repertory: this.$route.params.repertory,
                message_file_evaluation: this.$route.params.comment,
                amount: 1
            };
        },
        created :function(){
            this.message_file_img = require('../assets/img/' + this.book_id + '.jpg');
        },
        methods:{
            handleReduce: function() {
                if(this.amount == 1) return;
                this.amount--;
            },
            handleAdd: function() {
                this.amount++;
            },
            Carting: function(){
                if(!Confirm1())
                    return;
                if(!window.sessionStorage.getItem('token'))
                {
                    alert("此操作需要登录!");
                    return;
                }
                if(this.amount > this.repertory)
                {
                    alert('库存不足!');
                    return;
                }
                else
                {
                    var username = window.sessionStorage.getItem('user');
                    this.$axios.post('/CartStore',
                        {
                            book_id: this.book_id,
                            username: username,
                            bookname: this.bookname,
                            writer: this.writer,
                            price: this.price,
                            amount: this.amount
                        }
                    ).then((response)=>
                        {
                            if(response.data.token)
                            {
                                console.log(response.data)
                                this.$message(
                                    {
                                        message: '加入购物车成功',
                                        type: 'success'
                                    }
                                );
                            }
                        }
                    );
                    this.$router.push('/shelf');
                }
            },
            Ordering: function(){
                if(!Confirm2())
                {
                    return;
                }
                if(!window.sessionStorage.getItem('token'))
                {
                    alert("此操作需要登录!");
                    return;
                }
                if(this.amount > this.repertory)
                {
                    alert('库存不足!');
                    return;
                }
                var book_id = this.book_id;
                var bookname = this.bookname;
                var writer = this.writer;
                var username = window.sessionStorage.getItem('user');
                var createTime = parseInt((new Date()).getTime()/1000);
                var amount = this.amount;
                var money = this.price * this.amount;
                money += "";
                this.$axios.post('/OrderCreateByDetail',
                    {
                        book_id: book_id,
                        username: username,
                        bookname: bookname,
                        writer: writer,
                        create_time: createTime,
                        amount: amount,
                        money: money
                    }
                ).then((response)=>
                    {
                        if(response.data.token === true)
                        {
                            this.$message(
                                {
                                    message: '下单成功',
                                    type: 'success'
                                }
                            );
                            this.$router.push('/shelf');
                        }
                        else
                        {
                            this.$message(
                                {
                                    message: '创建订单时发生错误，请检查库存信息或用户是否被禁用',
                                    type: 'failure'
                                }
                            )
                        }
                    }
                );
            }
        }
    }
    function Confirm1(){
        if(window.confirm('你确定要将该商品加入购物车吗？'))
        {
            //alert("确定");
            return true;
        }else{
            //alert("取消");
            return false;
        }
    }
    function Confirm2(){
        if(window.confirm('你确定要直接购买该商品吗？'))
        {
            //alert("确定");
            return true;
        }else{
            //alert("取消");
            return false;
        }
    }
</script>

<style scoped src="../css/detailpage.css"></style>