<template>
    <div class="main">
        <div class="reg">
            <br>
            <div class="sch">
                &nbsp &nbsp &nbsp &nbsp <input type="text" class="se" placeholder="请输入用户名以查询" v-model="Search"/>
            </div>
            &nbsp &nbsp &nbsp &nbsp &nbsp &nbsp &nbsp <button @click="submit_user" class="order_btn">搜索</button>
            <br><br>
            <div style="height: 200px;width: 100%;background: none">
                <span style="font-size: 20px">&nbsp &nbsp 搜索结果:</span><br>
                &nbsp &nbsp &nbsp &nbsp<label>符合关键字的用户共有&nbsp<span>{{total}}</span>&nbsp位</label><br><br>
                <span style="font-size: 20px;color: #AE0000">&nbsp &nbsp 用户操作说明:</span><br>
                &nbsp &nbsp &nbsp &nbsp<label>1. 管理员用户可以禁用任意非管理员用户</label><br>
                &nbsp &nbsp &nbsp &nbsp<label>，但不能禁用管理员用户，包括自己</label><br>
                &nbsp &nbsp &nbsp &nbsp<label>2. 管理员用户可以解除任意非管理员用户</label><br>
                &nbsp &nbsp &nbsp &nbsp<label>的禁用状态</label><br>
                &nbsp &nbsp &nbsp &nbsp<label>3. 禁用状态的非管理员用户无法被提升为</label><br>
                &nbsp &nbsp &nbsp &nbsp<label>管理员用户</label><br>
            </div>
        </div>
        <div class="userlist" style="background: #DDDDFF">
                <table id="cartTable"  class="cart table table-condensed" >
                    <thead>
                    <tr style="background: cornflowerblue; color: white;">
                        <th style="width: 200px"><label>用户名</label></th>
                        <th style="width: 200px"><label>邮箱</label></th>
                        <th style="width: 200px"><label>电话</label></th>
                        <th style="width: 150px"><label>是否为管理员</label></th>
                        <th style="width: 150px"><label>是否禁用</label></th>
                        <th><label>操作</label></th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="(item, index) in Listdata">
                        <td class="goods">
                            <label>{{item.username}}</label>
                        </td>
                        <td class="goods">
                            <label>{{item.email}}</label>
                        </td>
                        <td class="goods">
                            <label>{{item.phone}}</label>
                        </td>
                        <td class="goods">
                            <label v-if="item.authorized">是</label>
                            <label v-else>否</label>
                        </td>
                        <td class="goods">
                            <label v-if="item.banned">是</label>
                            <label v-else>否</label>
                        </td>
                        <td>
                            <span class="delete btn btn-xs btn-primary" @click="do_ban(index)">禁用</span>&nbsp &nbsp<span class="delete btn btn-xs btn-primary" @click="undo_ban(index)">解禁</span>
                        </td>
                    </tr>

                    </tbody>
                </table>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Admin_user",
        data() {
            return{
                Search: null,
                Listdata: [],
                Userdata: [],
                total: null
            }
        },
        created() {
            this.$axios.post('/UserListGetter',
                {}
            ).then((response)=>{
                this.Userdata = response.data.tabledata;
                this.Listdata = response.data.tabledata;
                }
            );
        },
        methods:{
            submit_user() {
                var arr = [];
                this.total = 0;
                for(var i = 0; i < this.Userdata.length; i++){
                    if(this.Userdata[i].username.search(this.Search) != -1){
                        arr.push(this.Userdata[i]);
                        this.total++;
                    }
                }
                this.Listdata = arr;
            },
            do_ban(index) {
                var username = this.Listdata[index].username;
                if(this.Listdata[index].authorized == true){
                    alert("无法禁用管理员！");
                    return;
                }
                if(this.Listdata[index].banned == true){
                    alert("该用户已被禁用！");
                    return;
                }
                Confirm("do_ban");
                this.$axios.post('/BanOperator',
                    {
                        username: username,
                        banned: true
                    }
                ).then((response)=>{
                    if(response.data.token == true){
                        alert("操作成功");
                        window.location.reload();
                    }
                    }
                );
            },
            undo_ban(index) {
                var username = this.Listdata[index].username;
                if(this.Listdata[index].authorized == true){
                    alert("无法解禁管理员！");
                    return;
                }
                if(this.Listdata[index].banned == false){
                    alert("该用户未被禁用！");
                    return;
                }
                Confirm("undo_ban");
                this.$axios.post('/BanOperator',
                    {
                        username: username,
                        banned: false
                    }
                ).then((response)=>{
                        if(response.data.token == true){
                            alert("操作成功");
                            window.location.reload();
                        }
                    }
                );
            }
        }
    }
    function Confirm(buf){
        var content = "";
        if(buf == "do_ban") {content = "你确定要禁用该用户吗？";}
        if(buf == "undo_ban") {content = "你确定要解禁该用户吗？";}
        if(window.confirm(content))
        {
            //alert("确定");
            return true;
        }else{
            //alert("取消");
            return false;
        }
    }
</script>

<style scoped src="../css/admin.css"/>