<template>
    <div class="shelf">
        <div style="height: 40px;width: 500px;background: none;">
            <input type="text" v-model="search" class="search" placeholder="请输入关键字">{{search}}
            <button class="btn" @click="searching">搜索</button>
        </div>
        <el-table :data="Listdata"
                  style="margin-top: 5px;" @row-click="Intodetail">
            <el-table-column>
                    <!--<template slot="header"  scope="scope" >-->
                    <!--<el-input v-model="search" style="width: 300px"-->
                    <!--placeholder="请输入书名" prefix-icon="el-icon-search"/>-->
                    <!--</template>-->
                <el-table-column label="图片" width="70" align="center">
                    <template slot-scope="scope">
                        <img :src="scope.row.message_file_url ||defaultImg" style="width: 50px;height:50px;" @mouseover="imgOut(scope.row.message_file_url)" >
                    </template>
                </el-table-column>
                <el-table-column
                        prop="book_id"
                        label="编号"
                        sortable
                >
                </el-table-column>
                <el-table-column
                        prop="bookname"
                        label="书名"
                        sortable
                ></el-table-column>
                <el-table-column
                        prop="writer"
                        label="作者"
                        sortable
                ></el-table-column>
                <el-table-column
                        prop="repertory"
                        label="库存"
                        sortable
                ></el-table-column>
                <el-table-column
                        prop="price"
                        label="售价"
                        sortable
                ></el-table-column>
            </el-table-column>
        </el-table>

        <el-dialog width="400px" :visible.sync="imgVisible" class="img-dialog">
            <el-card :body-style="{ padding: '0px' }">
                <img :src="dialogImg" width="100%" height="100%">
            </el-card>
        </el-dialog>
    </div>
</template>

<script>
    import defaultImg from "../assets/7.jpg";
    export default {
        name: "Bookshelf",
        data() {
            return {
                search: this.$route.params.msg,
                defaultImg: defaultImg,
                dialogImg: defaultImg,
                imgVisible: false,
                tabledata:[],
                Listdata:[]
            }
        },
        created: function(){
            this.$axios.post('/BookListGetter',
                {}
            ).then((response) => {
                    this.tabledata = response.data.tabledata;
                    for(var i = 0; i < this.tabledata.length; i++)
                    {
                        this.tabledata[i].message_file_url = require('../assets/img/'+ this.tabledata[i].book_id +'.jpg');
                    }
                    this.Listdata = this.tabledata;
                }
            );
        },
        methods: {
            Intodetail:function(row){
                this.$router.push('/detail/'+ row.book_id + '/' + row.bookname + '/' + row.writer + '/' + row.repertory + '/' + row.price + '/' + row.comment);
            },
            searching: function () {
                var arr = [];
                for(var i = 0; i < this.tabledata.length; i++)
                {
                    if(this.tabledata[i].bookname.search(this.search) != -1)
                        arr.push(this.tabledata[i]);
                }
                this.Listdata = arr;
            },
            imgOut:function (url) {
                if (url) {
                    this.imgVisible = true;
                    this.dialogImg = url;
                }
            }
        },
        watch: {
            search(val, old_val){
                this.searching();
            }
        }
    }

</script>
<style scoped src="../css/bookshelfpage.css">
</style>
<style>
    ul li{
        list-style: none;
    }
    a:hover{
        color: darkmagenta;
    }
    .img-dialog {
        .el-dialog__header {
        padding: 0!important;
        }
    }
</style>