<template>
    <div class="main">
        <div class="reg">
            <br>
            <form>
                &nbsp &nbsp &nbsp<label><input type="checkbox" id="user" name="mode" @click="to_user"/>用户</label>
                <label><input type="checkbox" id="book" name="mode" @click="to_book"/>书</label>
            </form>
            <br>
            <div v-if="user_mode">
                <div class="sch">
                    &nbsp &nbsp &nbsp &nbsp <input type="text" class="se" placeholder="请输入用户名以查询" v-model="Search"/>
                </div>
                <span >&nbsp &nbsp &nbsp &nbsp 起始时间: </span>
                <select v-model="formData.startyear">
                <option v-for="item in year" :value="item" :key="item">{{item}}年</option>
                   </select>
                <select v-model="formData.startmonth" @change="getDay">
                   <option v-for="item in month" :value="item" :key="item">{{item}}月</option>
                </select>
                <select v-model="formData.startday">
                   <option v-for="item in day" :value="item" :key="item">{{item}}日</option>
                </select>
                <br>
                <span>&nbsp &nbsp &nbsp &nbsp 结束时间: </span>
                <select v-model="formData.endyear">
                    <option v-for="item in year" :value="item" :key="item">{{item}}年</option>
                </select>
                <select v-model="formData.endmonth" @change="getEndDay">
                    <option v-for="item in month" :value="item" :key="item">{{item}}月</option>
                </select>
                <select v-model="formData.endday">
                    <option v-for="item in day" :value="item" :key="item">{{item}}日</option>
                </select>
                <br>
                <br>
                &nbsp &nbsp &nbsp &nbsp &nbsp &nbsp &nbsp <button @click="submit_book" class="order_btn">搜索</button>
                <br><br><br>
                <div style="height: 200px;width: 100%;background: none">
                    <span style="font-size: 20px">&nbsp &nbsp 统计结果:</span><br>
                    &nbsp &nbsp &nbsp <label>用户 &nbsp<span>{{Search}}</span>&nbsp 在选定时间范围内，</label><br>
                    &nbsp &nbsp &nbsp <label>共有 &nbsp<span>{{total}}</span>&nbsp份订单</label><br>
                    &nbsp &nbsp &nbsp <label>累计购买书籍&nbsp<span>{{totalAmount}}</span>&nbsp本，交易总金额&nbsp<span>{{totalMoney}}</span>&nbsp元</label>
                </div>
            </div>
            <div v-else>
                <div class="sch">
                    &nbsp &nbsp &nbsp &nbsp <input type="text" class="se" placeholder="请输入书名以查询" v-model="Search"/>
                </div>
                <span >&nbsp &nbsp &nbsp &nbsp 起始时间: </span>
                <select v-model="formData.startyear">
                    <option v-for="item in year" :value="item" :key="item">{{item}}年</option>
                </select>
                <select v-model="formData.startmonth" @change="getDay">
                    <option v-for="item in month" :value="item" :key="item">{{item}}月</option>
                </select>
                <select v-model="formData.startday">
                    <option v-for="item in day" :value="item" :key="item">{{item}}日</option>
                </select>
                <br>
                <span>&nbsp &nbsp &nbsp &nbsp 结束时间: </span>
                <select v-model="formData.endyear">
                    <option v-for="item in year" :value="item" :key="item">{{item}}年</option>
                </select>
                <select v-model="formData.endmonth" @change="getEndDay">
                    <option v-for="item in month" :value="item" :key="item">{{item}}月</option>
                </select>
                <select v-model="formData.endday">
                    <option v-for="item in day" :value="item" :key="item">{{item}}日</option>
                </select>
                <br>
                <br>
                &nbsp &nbsp &nbsp &nbsp &nbsp &nbsp &nbsp <button @click="submit_book" class="order_btn">搜索</button>
                <br><br><br>
                <div style="height: 200px;width: 100%;background: none">
                    <span style="font-size: 20px">&nbsp &nbsp 统计结果:</span><br>
                    &nbsp &nbsp &nbsp<label>在选定的时间范围内，</label><br>
                    &nbsp &nbsp &nbsp<label>符合关键字的书籍共有&nbsp<span>{{total}}</span>&nbsp本</label><br>
                    &nbsp &nbsp &nbsp<label>累计出售量共&nbsp<span>{{totalAmount}}</span>&nbsp本，总计交易金额&nbsp<span>{{totalMoney}}</span>&nbsp元</label>
                </div>
            </div>
        </div>
        <div class="userlist" style="background: #DDDDFF">
                <table id="cartTable"  class="cart table table-condensed" >
                    <thead>
                    <tr style="background: cornflowerblue; color: white;">
                        <th><label>订单号</label></th>
                        <th><label>用户名</label></th>
                        <th style="width: 200px"><label>书名</label></th>
                        <th style="width: 200px"><label>作者</label></th>
                        <th style="width: 200px"><label>时间</label></th>
                        <th><label>数量</label></th>
                        <th><label>交易金额</label></th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="item in Listdata">
                        <td class="goods">
                            <label>{{item.order_id}}</label>
                        </td>
                        <td class="goods">
                            <label>{{item.username}}</label>
                        </td>
                        <td class="goods">
                            <label>{{item.bookname}}</label>
                        </td>
                        <td class="goods">
                            <label>{{item.writer}}</label>
                        </td>
                        <td class="goods">
                            <label>{{item.ctime_str}}</label>
                        </td>
                        <td class="number small-bold-red"><span>{{item.amount}}</span></td>
                        <td><span class="currency">￥</span><span id="priceTotal" class="large-bold-red">{{item.money}}</span></td>
                    </tr>

                    </tbody>
                </table>
            </div>
    </div>
</template>

<script>
    export default {
        name: "Admin_order",
        data() {
            return{
                Search: null,
                total: null,
                user_mode: null,
                Orderdata:[],
                Listdata:[],
                year:[],
                month:[],
                day:[],
                formData:{
                    startyear:2019,
                    startmonth:1,
                    startday:1,
                    endyear: 2019,
                    endmonth: 1,
                    endday:1
                },
                totalAmount: null,
                totalMoney: null
            }
        },
        created() {
            this.$axios.post('/OrderList',
                {}
            ).then((response)=> {
                    this.Orderdata = response.data.Orderdata;
                    this.Listdata = this.Orderdata;
                    console.log(this.Orderdata);
                    for(var i = 0; i < this.Orderdata.length; i++)
                    {
                        var date1 = new Date(this.Orderdata[i].create_time * 1000);
                        var date_value1 = date1.getFullYear() + '-' + (date1.getMonth() + 1) + '-' + date1.getDate() + ' ' + date1.getHours() + ':' + date1.getMinutes() + ':' + date1.getSeconds();//转换为正常时间格式
                        this.Orderdata[i].ctime_str = date_value1;
                    }
                }
            );
            this.init();
        },
        methods: {
            init(){
                this.getYear();
                this.getMonth();
                this.getDay();
            },
            getYear(){//获取年
                let date = new Date;
                let current_year = date.getFullYear();
                for (let i = 0; i < 20; i++) {
                    let y = current_year - i;
                    this.year.push(y);
                }
            },
            getMonth(){//获取月
                for (let i = 1; i < 13; i++) {
                    this.month.push(i);
                }
            },
            getDay(){//获取日
                this.day = [];
                let day = this.getDaysInMonth(this.formData.startyear, this.formData.startmonth)
                for (let i = 1; i <= day ; i++) {
                    this.day.push(i);
                }
            },
            getEndDay(){//获取日
                this.day = [];
                let day = this.getDaysInMonth(this.formData.endyear, this.formData.endmonth)
                for (let i = 1; i <= day ; i++) {
                    this.day.push(i);
                }
            },
            getDaysInMonth(year, month) {//获取某个月的天数
                month = parseInt(month, 10);
                let d = new Date(year, month, 0);
                return d.getDate();
            },
            submit_book() {
                var arr = [];
                this.totalAmount = 0;
                this.totalMoney = 0;
                for(var i = 0; i < this.Orderdata.length; i++)
                {
                    var date = new Date(this.Orderdata[i].create_time * 1000);
                    var year = date.getFullYear();
                    var month = date.getMonth() + 1;
                    var day = date.getDate();
                    var left = false, right = false;

                    console.log(year);
                    console.log(month);
                    console.log(day);
                    console.log(date.getHours());

                    if((year > this.formData.startyear)||
                        (year == this.formData.startyear && (month > this.formData.startmonth || (month == this.formData.startmonth && day >= this.formData.startday))))
                        left = true;

                    if((year < this.formData.endyear)||
                        (year == this.formData.endyear && (month < this.formData.endmonth || (month == this.formData.endmonth && day <= this.formData.endday))))
                        right = true;

                    if(left && right)
                    {
                        if(this.user_mode)
                        {
                            if(this.Orderdata[i].username == this.Search)
                            {
                                arr.push(this.Orderdata[i]);
                                this.totalAmount += this.Orderdata[i].amount;
                                this.totalMoney += this.Orderdata[i].money;
                            }
                        }
                        else
                        {
                            if (this.Orderdata[i].bookname.search(this.Search) != -1)
                            {
                                arr.push(this.Orderdata[i]);
                                this.totalAmount += this.Orderdata[i].amount;
                                this.totalMoney += this.Orderdata[i].money;
                            }
                        }
                    }
                }
                console.log(arr);
                console.log(this.Orderdata);
                this.total = arr.length;
                this.Listdata = arr;
            },
            to_user(){
                if(this.user_mode === null)
                {
                    this.user_mode = true;
                    this.Search = null;
                    this.total = null;
                    this.totalAmount = null;
                    this.totalMoney = null;
                    return;
                }
                if(this.user_mode === true)
                {
                    document.getElementById('user').checked = true;
                    return;
                }
                if(this.user_mode === false)
                {
                    document.getElementById('book').checked = false;
                    this.user_mode = true;
                    this.Search = null;
                    this.total = null;
                    this.totalAmount = null;
                    this.totalMoney = null;
                }
            },
            to_book(){
                if(this.user_mode === null)
                {
                    this.user_mode = false;
                    this.Search = null;
                    this.total = null;
                    this.totalAmount = null;
                    this.totalMoney = null;
                    return;
                }
                if(this.user_mode === false)
                {
                    document.getElementById('book').checked = true;
                    return;
                }
                if(this.user_mode === true)
                {
                    document.getElementById('user').checked = false;
                    this.user_mode = false;
                    this.Search = null;
                    this.total = null;
                    this.totalAmount = null;
                    this.totalMoney = null;
                }
            }
        }
    }
</script>

<style scoped src="../css/admin.css"/>
