<template>
  <div id="app">
    <div id="nav_admin" style="border-bottom:solid 2px #AAAAAA">
      <router-link v-if="!token" to="/Login">Login</router-link> &nbsp
      <router-link v-if="token" to="/Admin_user">User</router-link> &nbsp
      <router-link v-if="token" to="/Admin_book">Book</router-link> &nbsp
      <a @click="LOGOUT" style="float: right;margin-right: 40px;cursor: pointer">[logout]</a>
      <span v-if="token" style = "float: right;margin-right: 20px;color: yellow">Welcome to Ebook! Admin: {{username}}</span>
    </div>
    <router-view />
  </div>
</template>
<script>
  export default {
      name: 'App',
      data(){
          return{
          }
      },
      created() {
        console.log(this.authorized);
      },
      computed:{
          token: function (){
              return window.sessionStorage.getItem('token');
          },
          username: function () {
              return window.sessionStorage.getItem('user');
          },
          authorized: function () {
              return window.sessionStorage.getItem('authorized');
          }
      },
      methods:{
        LOGOUT: function () {
          window.sessionStorage.removeItem('token');
          window.sessionStorage.removeItem('user');
          window.sessionStorage.removeItem('authorized');
          window.location.reload();
        }
      }
  }
</script>
