      <div class="searchHeader">
        <form action="" class="parent">
          <input type="text" v-model="msg" id="search" class="search" placeholder="请输入关键字">
          <button name="" class="btn" @click="jump">搜索</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "home",
  data(){
    return{
      msg:''
    }
  },
  methods: {
    jump:function () {
      var txt = this.msg;
      this.$router.push({name: 'Bookshelf', params:{msg: txt}})
    }
  }
};
</script>

