import Vue from "vue";
import Router from "vue-router";
import store from "./store";
import Home from "./views/Home.vue";
import Bookshelf from "./views/Bookshelf";
import Login from "./views/Login";
import Register from "./views/Register";
import Detail from "./views/Detail";
import Cart from "./views/Cart";
import Order from "./views/Order";
import Admin_user from  "./views/Admin_user";
import Admin_book from "./views/Admin_book";
import Admin_order from "./views/Admin_order";

Vue.use(Router);

const router = new Router({
  mode: "hash",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/home",
      name: "Home",
      component: Home
    },
    {
      path: "/shelf",
      name: "Bookshelf",
      component: Bookshelf
    },
    {
      path: "/login",
      name: "Login",
      component: Login
    },
    {
      path: "/register",
      name: "Register",
      component: Register
    },
    {
      path: "/detail/:book_id/:bookname/:writer/:repertory/:price/:comment",
      name: "Detail",
      component: Detail
    },
    {
      path: "/order",
      name: "Order",
      component: Order,
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      }
    },
    {
      path: "/cart",
      name: "Cart",
      component: Cart,
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      }
    },
    {
      path: "/Admin_user",
      name: Admin_user,
      component: Admin_user
    },
    {
      path: "/Admin_book",
      name: Admin_book,
      component: Admin_book
    },
    {
      path: "/Admin_order",
      name: Admin_order,
      component: Admin_order
    }
  ]
});

router.beforeEach((to, from, next) => {
  const token = window.sessionStorage.getItem('token');
  if (to.meta.requireAuth) { // 判断该路由是否需要登录权限
    if (token) { // 通过vuex state获取当前的token是否存在
      next()
    } else {
      alert('该页面需要登录!');
      next({
        path: '/login',
        query: {redirect: to.fullPath} // 将跳转的路由path作为参数，登录成功后跳转到该路由
      })
    }
  } else {
    next()
  }
})

export default router
