<template>
    <div class="container">
        <table id="cartTable"  class="cart table table-condensed" >
            <thead>
            <tr>
                <th style="width:60px;"><label>选项</label></th>
                <th><label>书名</label></th>
                <th><label>作者</label></th>
                <th style="width:100px;"><label>单价</label></th>
                <th style="width:120px;"><label>数量</label></th>
                <th style="width:40px;"><label>操作</label></th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item, index) in Cartdata">
                <td ><input :id=index class="check-one check" type="checkbox" @click="check(index)"/></td>
                <td class="goods">
                    <label>{{item.bookname}}</label>
                </td>
                <td class="goods">
                    <label>{{item.writer}}</label>
                </td>
                <td class="number small-bold-red"><span>{{item.price}}</span></td>
                <td class="input-group">
                    <span class="input-group-addon minus" @click="handleReduce(index)" style="cursor: pointer">-</span>
                    <input type="text" class="number form-control input-sm" :value=item.amount />
                    <span class="input-group-addon plus" @click="handleAdd(index)" style="cursor: pointer">+</span>
                </td>
                <td class="operation"><span class="delete btn btn-xs btn-primary" @click="handleRemove(index)">删除</span></td>
            </tr>

            </tbody>
        </table>

        <div class="row">
            <div class="col-md-12 col-lg-12 col-sm-12">
                <div style="border-top:1px solid gray;padding:4px 10px;">
                    <div style="margin-left:20px;" class="pull-right total">
                        <label>金额合计:<span class="currency">￥</span><span id="priceTotal" class="large-bold-red">{{targetTotal}}</span></label>
                    </div>
                    <div class="pull-right">
                        <label>您选择了<span id="itemCount" class="large-bold-red" style="margin:0 4px;">1</span>种产品型号，共计<span id="qtyCount" class="large-bold-red" style="margin:0 4px;">{{targetAmount}}</span>件</label>
                    </div>
                    <div class="pull-right selected" id="selected">
                        <span id="selectedTotal"></span>
                    </div>
                </div>
            </div>
        </div>

        <div class="row">
            <button class="sbmt_order" @click="Ordering">确认购买</button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Cart",
        data() {
            return {
                Cartdata: [],
                targetAmount: null,
                targetPrice: null,
                targetTotal: null
            }
        },
        created: function () {
            var username = window.sessionStorage.getItem('user');
            this.$axios.post('/CartGetter',
                {
                    username: username,
                }
            ).then((response)=>{
                    console.log(response.data.Cartdata);
                    this.Cartdata = response.data.Cartdata;
                }
            );
        },
        computed: {},
        methods: {
            handleReduce: function (index) {
                if (this.Cartdata[index].amount == 1) return;
                this.Cartdata[index].amount--;
                this.check(index);
            },
            handleAdd: function (index) {
                this.Cartdata[index].amount++;
                this.check(index);
            },
            handleRemove: function (index) {
                var username = window.sessionStorage.getItem('user');
                var json_str = "";
                for (var i = 0; i < this.Cartdata.length; i++) {
                    if (i != index)
                    {
                        if(i > 1 || (i === 1 && index != 0))
                        {
                            json_str = json_str + ",";
                        }
                        json_str = json_str + "{\"book_id\":\"";
                        json_str = json_str + this.Cartdata[i].book_id;
                        json_str = json_str + "\",\"bookname\":\"";
                        json_str = json_str + this.Cartdata[i].bookname;
                        json_str = json_str + "\",\"writer\":\"";
                        json_str = json_str + this.Cartdata[i].writer;
                        json_str = json_str + "\",\"repertory\":";
                        json_str = json_str + this.Cartdata[i].repertory;
                        json_str = json_str + ",\"price\":";
                        json_str = json_str + this.Cartdata[i].price;
                        json_str = json_str + ",\"amount\":";
                        json_str = json_str + this.Cartdata[i].amount;
                        json_str = json_str + "}";
                    }
                }
                this.$axios.post('/CartRemove',
                        {
                            username: username,
                            json_str: json_str
                        }
                ).then((response)=>
                    {
                        if(response.data.token === true)
                        {
                            this.Cartdata.splice(index, 1);
                        }
                    }
                )
            },
            check(index) {
                if (document.getElementById(index).checked == false) {
                    this.targetAmount = null;
                    this.targetPrice = 0.00;
                    this.targetTotal = 0.00;
                    return;
                } else {
                    for (var i = 0; i < this.Cartdata.length; i++) {
                        var el = document.getElementById(i);
                        if (i != index)
                            if (el.checked == true)
                                el.checked = false;
                    }
                    this.targetAmount = this.Cartdata[index].amount;
                    this.targetPrice = this.Cartdata[index].price;
                    this.targetTotal = this.targetPrice * this.targetAmount;
                }
            },
            Ordering: function(){
                var book_id = "";
                var bookname = "";
                var writer = "";
                var username = window.sessionStorage.getItem('user');
                var createTime = parseInt((new Date()).getTime()/1000);
                var amount = 0;
                var money = 0.0;
                var json_str = "";

                var flag = false;
                for (var i = 0; i < this.Cartdata.length; i++) {
                    var el = document.getElementById(i);
                    if (el.checked === true)
                    {
                        book_id = this.Cartdata[i].book_id;
                        bookname = this.Cartdata[i].bookname;
                        writer = this.Cartdata[i].writer;
                        amount = this.Cartdata[i].amount;
                        money = this.targetTotal;
                        money += "";
                        flag = true;
                    }
                    else
                    {
                        if(i > 1 || (i === 1 && document.getElementById(0).checked === false))
                        {
                            json_str = json_str + ",";
                        }
                        json_str = json_str + "{\"book_id\":\"";
                        json_str = json_str + this.Cartdata[i].book_id;
                        json_str = json_str + "\",\"bookname\":\"";
                        json_str = json_str + this.Cartdata[i].bookname;
                        json_str = json_str + "\",\"writer\":\"";
                        json_str = json_str + this.Cartdata[i].writer;
                        json_str = json_str + "\",\"repertory\":";
                        json_str = json_str + this.Cartdata[i].repertory;
                        json_str = json_str + ",\"price\":";
                        json_str = json_str + this.Cartdata[i].price;
                        json_str = json_str + ",\"amount\":";
                        json_str = json_str + this.Cartdata[i].amount;
                        json_str = json_str + "}";
                    }
                }
                if(!flag)
                {
                    alert("请先选择你要购买的商品!");
                    return;
                }
                Confirm();
                        this.$axios.post('/OrderCreateByCart',
                            {
                                book_id: book_id,
                                username: username,
                                bookname: bookname,
                                writer: writer,
                                create_time: createTime,
                                amount: amount,
                                money: money,
                                json: json_str
                            }
                        ).then((response)=>
                            {
                                console.log(response.data);
                                if(response.data.token === true)
                                {
                                    this.$message(
                                        {
                                            message: '下单成功',
                                            type: 'success'
                                        }
                                    )
                                    this.$router.push('/shelf');
                                }
                                else
                                {
                                    this.$message(
                                        {
                                            message: '创建订单时发生错误，请检查库存信息或用户是否被禁用',
                                            type: 'failure'
                                        }
                                    )
                                }
                            }
                        );

            }
        }
    }
    function Confirm(){
        if(window.confirm('你确定要生成选中商品的订单吗？\n(生成订单后不可取消)'))
        {
            //alert("确定");
            return true;
        }else{
            //alert("取消");
            return false;
        }
    }
</script>

<style scoped>

    .sbmt_order{
        height:40px;
        width:120px;
        background-color: crimson;
        border: 1px solid crimson;
        font-family: 微软雅黑;
        border-radius: 5px;
        vertical-align: auto;
        font-size: 16px;
        color: white;
        cursor: pointer;
        float: right;
    }

    .sbmt_order:hover{
        background: #AE0000;
        border: #AE0000;
    }

</style>