<template>
    <div class="main">
        <div style="height: 120px;background: none;text-align: center;">
            <img src="../assets/7_副本.jpg" style="height: 100px;width: 100px;margin-top: 10px;"/>
        </div>
        <div style="height: 45px;background: none;text-align: center">
            <label style="height: 30px;font-size: 25px;font-family: 微软雅黑;color: black;font-weight: normal;">新用户注册</label>
        </div>
        <label for="" style="font-size:20px;font-family: 'Adobe 黑体 Std R';padding-left: 55px;color: black;font-weight: normal;" >用户名：</label>
        <div style="height:45px;width:400px;position:relative;display:inline" >
            <input id="username" type="text" class="input-box">
        </div>
        <br/>
        <label for="" style="font-size:20px;font-family: 'Adobe 黑体 Std R';padding-left: 75px;color: black;font-weight: normal;" > 密码：</label>
        <div style="height:45px;width:400px;position:relative;display:inline;" >
            <input id="password1" type="text" class="input-box">
        </div>
        <br/>
        <label for="" style="font-size:20px;font-family: 'Adobe 黑体 Std R';padding-left: 35px;color: black;font-weight: normal;" > 确认密码：</label>
        <div style="height:45px;width:400px;position:relative;display:inline;" >
            <input id="password2" type="text" class="input-box">
        </div>
        <br/>
        <label for="" style="font-size:20px;font-family: 'Adobe 黑体 Std R';padding-left: 75px;color: black;font-weight: normal;" > 邮箱：</label>
        <div style="height:45px;width:400px;position:relative;display:inline;" >
            <input id="email" type="text" class="input-box">
        </div>
        <br/>
        <label for="" style="font-size:20px;font-family: 'Adobe 黑体 Std R';padding-left: 55px;color: black;font-weight: normal;" > 手机号：</label>
        <div style="height:45px;width:400px;position:relative;display:inline;" >
            <input id="phone" type="text" class="input-box">
        </div>
        <div style="width:426px;margin-left: 135px;">
            <router-link to="/login">已有账号？去登录</router-link> &nbsp &nbsp
            <router-link to="/home">返回首页</router-link>
        </div>
        <br/>
        <div style="width:435px;">
            <button class="sbmt" @click="submitting">注册</button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Register",
        methods: {
            submitting: function () {
                var U = document.getElementById("username").value;
                var P1 = document.getElementById("password1").value;
                var P2 = document.getElementById("password2").value;
                var Phone = document.getElementById("phone").value;
                var Email = document.getElementById("email").value;
                if(U == "" || P1 == "" || P2 == "" || Phone == "" || Email== "" )
                {
                    alert("用户名或密码不能为空");
                    return;
                }
                if(P1 != P2)
                {
                    alert("两次输入密码不一致!");
                    return;
                }
                if(U.length > 10)
                {
                    alert("用户名最多只能有10位字符");
                    return;
                }
                if(Phone.length > 11 || Phone.length < 5)
                {
                    alert("手机号不符合规范!");
                    return;
                }
                if(Email.indexOf("@") <= -1)
                {
                    alert("邮箱不符合规范");
                    return;
                }
                this.$axios.post('/Register',
                    {
                        username: U,
                        password: P1,
                        phone: Phone,
                        email: Email
                    }
                ).then((response)=>
                    {
                        if(response.data.token === true)
                        {
                            this.$message(
                                {
                                    message: '注册成功',
                                    type: 'success'
                                }
                            );
                            this.$router.push('/login');
                        }
                        else
                        {
                            this.$message(
                                {
                                    message: '用户名已存在',
                                    type: 'failure'
                                }
                            );
                        }
                    }
                );
            }
        }
    }
</script>

<style scoped src="../css/registerpage.css"></style>
<style scoped>
    a{
        text-decoration: none;
        color: #888888;
        cursor: pointer;
    }
    a:hover{
        color: #FF5511;
    }
</style>