<template>
    <div class="container">
        <table id="cartTable"  class="cart table table-condensed" >
            <thead>
            <tr style="background: cornflowerblue; color: white;">
                <th><label>订单号</label></th>
                <th><label>书名</label></th>
                <th><label>作者</label></th>
                <th><label>时间</label></th>
                <th style="width:100px;"><label>数量</label></th>
                <th style="width:200px;"><label>交易金额</label></th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in Listdata">
                <td class="goods">
                    <label>{{item.order_id}}</label>
                </td>
                <td class="goods">
                    <label>{{item.bookname}}</label>
                </td>
                <td class="goods">
                    <label>{{item.writer}}</label>
                </td>
                <td class="goods">
                    <label>{{item.ctime_str}}</label>
                </td>
                <td class="number small-bold-red"><span>{{item.amount}}</span></td>
                <td><span class="currency">￥</span><span id="priceTotal" class="large-bold-red">{{item.money}}</span></td>
            </tr>

            </tbody>
        </table>
        <div class="row">
            <div class="col-md-12 col-lg-12 col-sm-12">
                <div style="border-top:1px solid gray;padding:4px 10px;">
                    <div class="pull-right">
                        <label>根据日期信息查询，共有<span id="qtyCount" class="large-bold-red" style="margin:0 4px;">{{total}}</span>份订单</label>
                    </div>
                </div>
            </div>
        </div>
        <div class="row" style="position: relative">
            <i class="el-icon-search" style="height: 30px;width: 30px;position: absolute;right: 230px;top: 10px"></i>
            <input type="text" class="search" placeholder="请输入日期以查询订单" v-model="Search"/>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Order",
        data(){
            return{
                Search: null,
                total: 0,
                Orderdata:[],
                Listdata:[]
            }
        },
        created: function ()
        {
            var username = window.sessionStorage.getItem('user');
            this.$axios.post('/OrderListByUsername',
                {
                    username: username
                }
            ).then((response)=> {
                    this.Orderdata = response.data.Orderdata;
                    this.Listdata = this.Orderdata;
                    this.total = this.Orderdata.length;
                    console.log(this.Orderdata);
                    for(var i = 0; i < this.Orderdata.length; i++)
                    {
                        var date1 = new Date(this.Orderdata[i].create_time * 1000);
                        var date_value1 = date1.getFullYear() + '-' + (date1.getMonth() + 1) + '-' + date1.getDate() + ' ' + date1.getHours() + ':' + date1.getMinutes() + ':' + date1.getSeconds();//转换为正常时间格式
                        this.Orderdata[i].ctime_str = date_value1;
                    }
                }
            );
        },
        computed:{

        },
        methods: {
            timeTrans:function(index){
                var date1 = this.Orderdata[index].create_time;
                var date_value1 = date1.getFullYear() + '-' + (date1.getMonth() + 1) + '-' + date1.getDate() + ' ' + date1.getHours() + ':' + date1.getMinutes() + ':' + date1.getSeconds();//转换为正常时间格式
                return date_value1;
            },
            searching:function(){
                var arr = [];
                for(var i = 0; i < this.Orderdata.length; i++)
                {
                    if(this.Orderdata[i].ctime_str.search(this.Search) != -1)
                    {
                        arr.push(this.Orderdata[i]);
                    }
                }
                this.total = arr.length;
                this.Listdata = arr;
            }
        },
        watch:{
            Search(val, oldval){
                this.searching();
            }
        }
    }
</script>

<style scoped>
    .search {
        width: 270px;
        height: 38px;
        outline: none;
        border: 1px solid crimson;
        background: none;
        position: absolute;
        font-size: 18px;
        text-indent: 30px;
        right: 0;
    }
</style>