<template>
    <div class="main">
        <div class="tle">
            <label style="font-size: 40px">欢 声</label>
            <label style="font-size: 40px">迎 书</label>
            <label style="font-size: 40px">使 管</label>
            <label style="font-size: 40px">用 理</label>
            <label style="font-size: 40px">智 员</label>
            <label style="font-size: 40px">能 系</label>
            <label style="font-size: 40px">有 统</label>
        </div>
        <div class="picture">
            <img src="" style="height: 100%;width: 100%;"/>
        </div>
        <div class="login">
            <br>
            <br>
            <div style="height: 60px;background: none;text-align: left">
                <label style="height: 30px;font-size: 25px;font-family: 微软雅黑;color: black;padding-left: 50px;font-weight: normal;">密码登录</label>
            </div>
            <br>
            <label for="" style="font-size:20px;font-family: 'Adobe 黑体 Std R';padding-left: 55px;color: black;font-weight: normal;" >用户名：</label>
            <div style="height:45px;width:400px;position:relative;display:inline" >
                <input id="username" type="text" class="input-box">
            </div>
            <br/>
            <br/>
            <label for="" style="font-size:20px;font-family: 'Adobe 黑体 Std R';padding-left: 75px;color: black;font-weight: normal;" > 密码：</label>
            <div style="height:45px;width:400px;position:relative;display:inline;" >
                <input id="password" type="password" class="input-box">
            </div>
            <br/>
            <br/>
            <div style="width:426px;margin-left: 135px;">
                <a herf="找回密码网址">忘记密码？</a>
            </div>
            <br/>
            <br/>
            <div style="width:435px;">
                <button class="sbmt" @click="submitting">登录</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Login",

        methods:{
            submitting:function () {
                var U = document.getElementById("username").value;
                var P = document.getElementById("password").value;
                if(U == "" || P == "")
                {
                    alert("用户名或密码不能为空");
                    return;
                }
                if(U.length > 12)
                {
                    alert("用户名最多只能有12位字符");
                    return;
                }
                if(window.sessionStorage.getItem('token'))
                {
                    alert("你已经登录， 不能重复登录!");
                    return;
                }
                try{
                    this.$axios.post('/Login',
                        {
                            username: U,
                            password: P
                        }
                    ).then((response)=>
                        {
                            console.log(response.data);
                            if (response.data.isBanned === true)
                            {
                                this.$message(
                                    {
                                        message: '该用户已被禁用',
                                        type: 'failure'
                                     }
                                 );
                                return;
                            }
                            else
                                if(response.data.token === true){
                                window.sessionStorage.setItem('token',response.data.token);
                                window.sessionStorage.setItem('user', response.data.user);
                                if(response.data.isAuthorized === true) {
                                    window.sessionStorage.setItem('authorized', response.data.isAuthorized);
                                    this.$message(
                                        {
                                            message: '管理员登录成功',
                                            type: 'success'
                                        }
                                    );
                                    this.$router.push('/Admin_user');
                                }
                                else {
                                    this.$message(
                                        {
                                            message: '该账号不是一个管理员账号',
                                            type: 'failure'
                                        }
                                    );
                                    return;
                                }
                            }
                            else
                            {
                                this.$message(
                                    {
                                        message: '用户名或密码错误',
                                        type: 'failure'
                                    }
                                );
                            }
                        }
                    );
                } catch (err) {
                    console.error(`Error received from axios.post: ${JSON.stringify(err)}`);
                }
            }
        }
    }
</script>

<style src="../css/loginpage.css" scoped></style>
<style scoped>
    a{
        text-decoration: none;
        color: #888888;
        cursor: pointer;
    }
    a:hover{
        color: #FF5511;
    }
</style>