<template>
    <div class="main">
        <div class="reg"><br>
            <div class="sch">
                &nbsp &nbsp &nbsp &nbsp <input type="text" class="se" placeholder="请输入书名以查询" v-model="Search"/>
            </div>
            &nbsp &nbsp &nbsp &nbsp &nbsp &nbsp &nbsp <button @click="submit_book" class="order_btn">搜索</button>
            <br><br>
            <div style="height: 200px;width: 100%;background: none">
                <span style="font-size: 20px">&nbsp &nbsp 搜索结果:</span><br>
                &nbsp &nbsp &nbsp &nbsp<label>符合关键字的用户共有&nbsp<span>{{total}}</span>&nbsp位</label><br><br>
                <span style="font-size: 20px;color: #AE0000">&nbsp &nbsp 书籍管理操作说明:</span><br>
                &nbsp &nbsp &nbsp &nbsp<label>1. 管理员用户可以封禁任意一本有声书</label><br>
                &nbsp &nbsp &nbsp &nbsp<label>2. 管理员用户可以解除任意一本有声书</label><br>
                &nbsp &nbsp &nbsp &nbsp<label>的封禁状态</label><br>
                &nbsp &nbsp &nbsp &nbsp<label>3. 禁用状态的书籍将无法被任意用户阅</label><br>
                &nbsp &nbsp &nbsp &nbsp<label>读或浏览</label><br>
                &nbsp &nbsp &nbsp &nbsp<label>4. 管理员用户可以修改任意一本有声书</label><br>
                &nbsp &nbsp &nbsp &nbsp<label>的相关信息</label><br>
            </div>
        </div>
        <div class="userlist" style="background: #DDDDFF">
                <table id="cartTable"  class="cart table table-condensed" >
                    <thead>
                    <tr style="background: cornflowerblue; color: white;">
                        <th><label>编号</label></th>
                        <th><label>书名</label></th>
                        <th><label>创建者</label></th>
                        <th><label>地区</label></th>
                        <th><label>归类</label></th>
                        <th><label>操作</label></th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="(item,index) in Listdata">
                        <td class="goods">
                            <label>{{item.book_id}}</label>
                        </td>
                        <td class="goods">
                            <label>{{item.bookname}}</label>
                        </td>
                        <td class="goods">
                            <label>{{item.creator}}</label>
                        </td>
                        <td class="goods">
                            <label>{{item.region}}</label>
                        </td>
                        <td class="goods">
                            <label>{{item.bookclass}}</label>
                        </td>
                        <td>
                            <span class="delete btn btn-xs btn-primary" @click="change(index)">更改</span>&nbsp &nbsp<span class="delete btn btn-xs btn-primary" @click="submit_ban(index)">封禁</span>&nbsp &nbsp<span class="delete btn btn-xs btn-primary" @click="submit_recover(index)">恢复</span>
                        </td>
                    </tr>

                    </tbody>
                </table>
                <el-dialog width="400px" :visible.sync="isVisible">
                    <form>
                        &nbsp &nbsp 编号: &nbsp <span>{{book_id}}</span><br>
                        &nbsp &nbsp 书名: &nbsp <input type="text" style="width: 250px" v-model="bookname"/><br>
                        &nbsp &nbsp 作者: &nbsp <span>{{creator}}</span><br>
                        &nbsp &nbsp 库存: &nbsp <input type="text" style="width: 250px" v-model="region"/><br>
                        &nbsp &nbsp 售价: &nbsp <input type="text" style="width: 250px" v-model="bookclass"/><br>
                    </form>
                    <br>
                    &nbsp &nbsp &nbsp &nbsp &nbsp &nbsp &nbsp &nbsp &nbsp &nbsp<button @click="submit_new" class="order_btn">提交</button>
                </el-dialog>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Admin_book",
        data() {
            return {
                Search: null,
                tabledata:[],
                Listdata:[],
                isVisible: false,
                book_id: null,
                bookname: null,
                creator: null,
                bookclass: null,
                region: null
            }
        },
        created() {
            this.$axios.post('/BookListGetter',
                {}
            ).then((response)=>{
                    this.tabledata = response.data.tabledata;
                    this.Listdata = response.data.tabledata;
                    console.log(response.data.tabledata);
                }
            );
        },
        methods:{
            submit_book(){
                var arr = [];
                for(var i = 0; i < this.tabledata.length; i++){
                    if(this.tabledata[i].bookname.search(this.Search) != -1){
                        arr.push(this.tabledata[i]);
                    }
                }
                this.Listdata = arr;
            },
            newBook(){
                this.book_id = null;
                this.bookname = null;
                this.creator = null;
                this.region = null;
                this.bookclass= null;
            },
            newTable(){
                this.isVisible = true;
            },
            change(index){
                this.book_id = this.Listdata[index].book_id;
                this.bookname = this.Listdata[index].bookname;
                this.creator = this.Listdata[index].creator;
                this.region = this.Listdata[index].region;
                this.bookclass = this.Listdata[index].class;
                this.newTable();
            },
            submit_new(){
                this.$axios.post('/UpdateBook',
                    {
                        book_id: this.book_id,
                        bookname: this.bookname,
                        creator: this.creator,
                        region: this.region,
                        bookclass: this.bookclass
                    }
                )
                .then((response) =>{
                    if(response.data.token == true){
                        alert("修改成功！");
                        window.location.reload();
                    }
                }
                );
            },
            submit_ban(index){
                let book_id = this.Listdata[index].book_id;
                this.$axios.post('/BanBook',
                    {
                        book_id: book_id
                    }
                ).then((response)=>{
                        if(response.data.token == true){
                            alert("封禁成功");
                            window.location.reload();
                        }
                    }
                );
            },
            submit_recover(index){
                let book_id = this.Listdata[index].book_id;
                this.$axios.post('/RecoverBook',
                    {
                        book_id: book_id
                    }
                ).then((response)=>{
                        if(response.data.token == true){
                            alert("恢复成功");
                            window.location.reload();
                        }
                    }
                );
            }
        }
    }
</script>

<style scoped src="../css/admin.css"/>